let accountInit = function () {
    jQuery.validator.setDefaults({

        errorElement: "span",
        errorPlacement: function (error, element) {
            error.addClass("help-block");

            if (element.prop("type") === "checkbox") {
                error.insertAfter(element.parent());
            }
            else if (element.prop("type") === "radio") {
                error.insertAfter(element.parent().parent());
            }
            else {
                error.insertAfter(element);
            }
        },
        highlight: function (element, errorClass, validClass) {
            console.log(element);
            $(element).parents('.form-group').addClass("has-error").removeClass("has-success");
        },
        unhighlight: function (element, errorClass, validClass) {
            $(element).parents('.form-group').addClass("has-success").removeClass("has-error");
        }
    });

    const swalWithBootstrapButtons = Swal.mixin({
        customClass: {
            confirmButton: 'btn form-submit ml-2 mr-2',
            cancelButton: 'btn form-submit mr-2 ml-2'
        },
        buttonsStyling: false,
    })

    $('.delete-card').click(function () {

        swalWithBootstrapButtons.fire({
            title: 'Emin misiniz?',
            text: "Kredi kartınız silinecek.",
            type: '',
            showCancelButton: true,
            confirmButtonText: 'Evet',
            cancelButtonText: 'Vazgeç',
            reverseButtons: true
        }).then((result) => {
            if (result.value) {
                swalWithBootstrapButtons.fire(
                    {
                        title: 'Kredi kartınız silindi.',
                        text: "",
                        type: 'success',
                        showCancelButton: false,
                        showConfirmButton: false,
                        timer: 1500
                    }
                )
            } else if (result.dismiss === Swal.DismissReason.cancel) {
                Swal.close()
            }
        })
    });



    $('.add-new-card').click(function () {
        $('#newCardModal .modal-title').text('Yeni Kart Ekle');
        $('#newCardModal').modal('show');
        $('.new-credit-card-form').trigger('reset');
    });

    $('.edit-card').click(function () {
        $('#newCardModal .modal-title').text('Kartı Düzenle');
        $('#newCardModal input[name="payment[cc_number]"]').val('4949484920383901');
        $('#newCardModal select[name="payment[cc_exp_month]"]').val('01');
        $('#newCardModal select[name="payment[cc_exp_year]"]').val('2019');
        $('#newCardModal input[name="payment[cc_cid]"]').val('323');
        $('#newCardModal').modal('show')
    });

    $(".new-credit-card-form").validate({
        ignore: [],
        rules: {
            'payment[cc_exp_month]': {
                required: true
            },
            'payment[cc_exp_year]': {
                required: true
            },
            'payment[cc_number]': {
                required: true
            },
            'payment[cc_cid]': {
                required: true,
                number: true,
                minlength: 3
            }
        },
        messages: {
            'payment[cc_number]': {
                required: 'Lütfen geçerli bir kart numarası giriniz.',
                minlength: 'Lütfen geçerli bir kart numarası giriniz.',
                maxlength: 'Lütfen geçerli bir kart numarası giriniz.',
                number: 'Lütfen geçerli bir kart numarası giriniz.'
            },
            'payment[cc_exp_month]': {
                required: 'Lütfen seçin.'
            },
            'payment[cc_exp_year]': {
                required: 'Lütfen seçin.'
            },
            'payment[cc_cid]': {
                required: 'Lütfen güvenlik numarasını yazın.',
                number: 'Lütfen geçerli birgüvenlik numarası yazın.'
            }
        }
    });

    $(".renew-password-form").validate({
        rules: {
            current_password: {
                required: true
            },
            password: {
                required: true
            },
            password_confirmation: {
                required: true,
                equalTo: '#new-password'
            }
        },
        messages: {}
    });


    $(".account-form").validate({
        ignore: [],
        rules: {
            ignore: [],
            firstname: {
                required: true,
                lettersonly: true,
                minlength: 2
            },
            lastname: {
                required: true,
                lettersonly: true,
                minlength: 2
            },
            email: {
                required: true,
                emaildomain: true
            },
            phone: {
                required: true
            },
            gender: {
                required: true
            },
            uyelik_sozlesmesi: {
                required: true
            }
        },
        messages: {
            'gender': {
                required: "Lütfen seçin."
            },
            'uyelik_sozlesmesi': {
                required: "Lütfen onaylayın."
            }
        }
    });


    $(".new-address-form").validate({
        rules: {
            'firstname': {
                required: true,
                minlength: 2,
                lettersonly: true
            },
            'lastname': {
                required: true,
                minlength: 2,
                lettersonly: true
            },
            'telephone': {
                required: true,
                number: true
            },
            'city': {
                required: true
            },
            'region': {
                required: true
            },
            'street': {
                required: true,
                minlength: 5
            },
            'title_address': {
                required: true
            }
        },
        messages: {
            'telephone': {
                number: "Lütfen geçerli bir telefon giriniz"
            }
        }
    });

    $(".edit-address-form").validate({
        rules: {
            'first_name_edit': {
                required: true,
                minlength: 2,
                lettersonly: true
            },
            'last_name_edit': {
                required: true,
                minlength: 2,
                lettersonly: true
            },
            'phone_number_edit': {
                required: true,
                number: true
            },
            'city_edit': {
                required: true
            },
            'district_edit': {
                required: true
            },
            'address_edit': {
                required: true,
                minlength: 5
            },
            'address_name_edit': {
                required: true
            }
        },
        messages: {
            'phone_number_edit': {
                number: "Lütfen geçerli bir telefon giriniz"
            }
        }
    });

    $(".contact-form").validate({
        rules: {
            first_name: {
                required: true,
                lettersonly: true
            },
            last_name: {
                required: true,
                lettersonly: true
            },
            phone_number: {
                required: true
            },
            department: {
                required: true
            },
            subject: {
                required: true
            },
            message: {
                required: true
            }
        }
    });

    // Copy coupon code to clipboard
    $('.copy-code').click(function () {
        let code = $(this).closest('.coupon-box').find('.coupon-code').text();
        copyStringToClipboard(code);
        new Noty({
            text: '<i class="fa fa-clone mr-2"></i> Kupon kodu kopyalandı.',
            layout: 'bottomRight',
            timeout: 1000
        }).show();
    });

    $(".subscribe-form").validate({
        ignore: [],
        rules: {
            'newsletter_kvkk': {
                required: true
            },
        },
        messages: {
            'newsletter_kvkk': {
                required: "Lütfen onaylayın"
            }
        }
    });
};
App.prototype.pageDetector('account-page-wrapper', accountInit);
