$('.remove-from-favourites-button').click(function (e) {
    e.preventDefault();
    let selectedProduct = $(this).closest('.product-box');
    //Ajax request
    selectedProduct.parent().remove();
    new Noty({
        text: '<i class="fa fa-heart-broken mr-2"></i> Ürün favorilerinizden çıkartıldı.',
        layout: 'bottomRight',
        timeout: 2000
    }).show();
});